@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
body{margin:0;font-family:'Montserrat', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#FAFAFA}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.nav-bar{width:100%;padding-top:70px}.nav-bar-content{height:70px;width:100%;min-height:25px;background-color:#37BBBD;top:0;position:fixed;display:flex;z-index:2}.logo-container{width:20%;height:100%;position:relative}.links-container{width:80%;position:relative;display:flex;justify-content:flex-end;align-items:center}.logo{height:90%;max-width:100%;left:15%;position:absolute;object-fit:contain}.link-title{margin-right:40px;font-weight:bold;font-size:18px;text-decoration:none;color:#FAFAFA}.link-title:hover{text-decoration:underline}

.App{text-align:center;width:100%;height:100%;margin-bottom:50px;font-size:24px}@media screen and (max-width: 800px){.App{font-size:14px}}.app-body{top:0px;margin-left:7%;margin-right:7%}a:link,a:visited{text-decoration:none}.button-primary{padding:15px;padding-left:35px;padding-right:35px;border-radius:10px;background-color:#37BBBD;color:white;font-size:18px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.profile-photo-container{float:right;margin-bottom:25px;margin-left:25px;width:305px;height:390px;border-radius:10px;position:relative}.profile-photo-container .profile-photo-img{border-radius:10px;width:305px;height:390px;object-fit:cover;object-position:top}.profile-photo-container:hover{transition:.5s ease-out}.profile-photo-container:hover:before{content:'';position:absolute;left:0;top:0;width:100%;height:100%;display:inline-block;border-radius:10px;background:rgba(55,187,189,0);transition:.5s ease-out}.profile-photo-container:not(:hover){-webkit-filter:brightness(95%) grayscale(40%) contrast(160%) saturate(250%);filter:brightness(95%) grayscale(40%) contrast(160%) saturate(250%);transition:.5s ease-out}.profile-photo-container:not(:hover):before{content:'';position:absolute;left:0;top:0;width:100%;height:100%;display:inline-block;border-radius:10px;background:rgba(55,187,189,0.35);transition:.5s ease-out}.center-container{display:flex;justify-content:center}.pink{color:#E02657}.turquoise{color:#37BBBD}.black{color:black}.section{padding-top:50px;padding-bottom:50px}.align-left{text-align:left}.normal-text{line-height:2.5rem}

.footer{width:100%;padding-top:calc(max(25px, 5vh))}.footer-content{position:fixed;height:5vh;bottom:0;background-color:#00070F;color:#FAFAFA;width:100%;min-height:25px;z-index:2}.footer-list-container{position:relative}.footer-lists{position:absolute;margin-top:0px;margin-bottom:0px;padding:0px;margin-left:2%;margin-right:2%;margin-top:0.5%}.language-list{left:0px}.language-list li{display:inline;padding-right:15px;font-size:12px}.contact-list{right:0px}.contact-list a:link,.contact-list a:visited{color:white}.contact-list li{display:inline;padding-left:20px;font-size:25px}.a:link a:visited{margin:0px}

.projects{top:0px}.header{width:100%;display:flex}.title{font-weight:bold;font-size:32px;text-align:left;width:50%}.filters{display:flex;justify-content:flex-end;text-align:right;width:50%;font-size:25px}.filter-item{list-style-type:none;margin-left:5px}.projects-list{display:flex;flex-wrap:wrap;justify-content:space-evenly}.item{margin:2%}

.thumbnail{width:400px;height:260px;border-radius:10px;object-fit:cover;transition:1.5s ease-out}.gradient-background{position:relative;display:inline-block;overflow:hidden;border-radius:10px}.gradient-background:after{content:'';position:absolute;left:0;top:0;width:100%;height:99%;display:inline-block;border-radius:10px;box-shadow:0 1000px rgba(5,244,252,0.13) inset;background:linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(247,252,252,0.0416667) 60%, #37bbbd 100%);transition:background 0.5s ease-out}.card-text{text-align:justify;text-justify:auto;color:#FAFAFA;font-weight:500;margin-left:5%;margin-right:5%;z-index:1}.card-name{position:absolute;bottom:0;font-weight:600;font-size:18px;transition:0.5s}.card-info{position:absolute;pointer-events:none;top:100%;font-size:16px;margin-top:0px;opacity:0;transition:0.5s;height:80%}.gradient-background:hover{cursor:pointer}.gradient-background:hover img{-webkit-filter:blur(5px);filter:blur(5px);transition:1s ease-out}.gradient-background:hover .card-name{text-decoration:underline;bottom:85%;transform:translateY(100%)}.gradient-background:hover .card-info{top:20%;opacity:1}.gradient-background:hover:after{background:linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(247,252,252,0.0416667) 40%, #37bbbd 100%),rgba(55,187,189,0.9);position:absolute}.card-tag-list{position:inherit;display:flex;bottom:15%}.card-tag{border:1px solid #FAFAFA;border-radius:45px;padding:5px;padding-left:10px;padding-right:10px;margin-right:10px}

.project-details-container{display:flex;width:100%}.project-infos{text-align:justify;font-size:18px}.project-image-list{margin-left:5%;display:flex;flex-direction:column;align-items:flex-end}.project-image{margin-bottom:25px;width:340px;height:245px}.tag-list{position:inherit;display:flex;bottom:15%;color:#E02657;font-size:16px}.tag{border:1px solid #E02657;border-radius:45px;padding:5px;padding-left:10px;padding-right:10px;margin-right:10px}.breadcrumb{display:flex;justify-content:flex-start;color:black;font-weight:400;opacity:0.5;font-size:18px}.breadcrumb a:visited{color:black}.redirect-link{margin-top:50px;margin-right:15px}.project-description{white-space:pre-wrap}

.contact-page{height:100%}.contact-text{text-align:left}.contact-container{margin-top:80px;display:flex;justify-content:center;width:100%;height:100%}.contact-links{display:flex;flex-direction:column;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;align-items:flex-start;justify-content:center}.contact{display:flex;margin-bottom:20px;align-items:center}.contact-icon{font-size:50px;margin-right:15px}.contact-text{color:black;font-size:20px}

.home{text-align:start;margin-top:60px}.home-section{padding-top:50px;padding-bottom:50px}.headline{font-size:60px;margin-top:0px}.home-text{line-height:2.5rem}.turquoise-background{background:rgba(55,187,189,0.06);width:100%;left:20%;margin-left:-20vw;margin-top:-50px;position:absolute;z-index:-2}.home-inline-list{padding:0;display:flex;list-style-type:none;justify-content:center;align-items:center;margin-top:50px;margin-bottom:70px}.projects-item{width:50%;margin-left:50px;margin-right:50px}.home-contact-item{margin-left:25px;margin-right:25px}

.technologies-list{padding:0px;margin-top:50px;margin-bottom:70px;text-align:left}.technologies-item{width:33%;padding-right:50px;display:flex;flex-direction:column}.technologies-icons{color:#E02657;font-size:50px}

.about-headline{text-align:start;margin-top:60px}.about-historic{display:flex;flex-direction:column}.about-category{margin-top:50px}.icon-container{min-width:80px;margin-right:15px}.timeline-icon{right:0px;min-width:80px;max-width:80px;font-size:70px;color:#007db8;height:auto}.timeline-dot{min-width:80px;font-size:40px;color:#E02657;margin-right:15px}.about-list{padding:0px}.timeline-item{display:flex;margin-bottom:55px}.timeline-description{display:flex;flex-direction:column}.timeline-title{font-weight:600}.timeline-date{font-size:18px;line-height:22px}.languages-section{display:flex}.languages-col{margin:12px;display:flex;flex-direction:column;width:50%}

.not-found{height:70vh}.not-found-container{display:flex;align-items:center;justify-content:center;flex-direction:column}

